import React, { Fragment } from 'react'
import { InView } from 'react-intersection-observer'
import { motion } from 'framer-motion'


export default () => {
  const [imageLoaded, setImageLoaded] = React.useState(false)

  const variant = {
    hidden: { x: 400, opacity: 0, scale: 0.2 },
    visible: { x: 0, opacity: 1, scale: 1 }
  }

  const loadingDone = () => {
    setImageLoaded(true)
  }

  return (
    <Fragment>
      <div style={imageLoaded ? styles.spinnerHide : styles.spinnerShow}>
        <h4 style={styles.spinnerStyle} className="animate-flicker">Loading stack...</h4>
      </div>
      <InView threshold={0.15} triggerOnce={true}>
        {({ ref, inView }) => (
          <motion.div ref={ref} variants={variant} initial="hidden" animate={inView ? "visible" : "hidden"} transition={{ duration: 1 }} style={styles.tech} >
            <motion.img onLoad={loadingDone} style={imageLoaded ? styles.showImage : styles.hideImage} width="100%" src="/assets/images/react.png" alt="mern stack" />
            <motion.img style={imageLoaded ? styles.showImage : styles.hideImage} width="100%" src="/assets/images/mongo.png" alt="mern stack" />
            <motion.img style={imageLoaded ? styles.showImage : styles.hideImage} width="100%" src="/assets/images/express.png" alt="mern stack" />
            <motion.img style={imageLoaded ? styles.showImage : styles.hideImage} width="100%" src="/assets/images/node.png" alt="mern stack" />
            <hr style={{ marginBottom: 50 }} />
          </motion.div>
        )}
      </InView>
    </Fragment>
  )
}

const styles = {
  tech: {
    padding: window.innerWidth > 500 ? "50px 150px" : 0,
    position: "relative",
    left: 0
  },

  hideImage: {
    visiblity: "hidden",
    marginBottom: 70
  },

  showImage: {
    visibility: "visible",
    marginBottom: 70
  },

  spinnerShow: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },

  spinnerHide: {
    display: "none",
  },

  spinnerStyle: {
    marginTop: 300,
    position: "relative",
  }
}